}

.react-select__control {
  border-width: 1px;
  --tw-border-opacity: 1;
  border-color: rgba(209, 213, 219, var(--tw-border-opacity));
  display: block;
  border-radius: 0 !important;
  width: 100%;
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  padding: 0.25rem;
}

.ratio-1-1 {
  width: 100%;
  padding-top: 100%;
  position: relative;
}

.noreset p {
  margin: 1em 0;
}

.noreset blockquote,
.noreset figure {
  margin: 1em 40px;
}

.noreset h1 {
  font-size: 2em;
  font-weight: bold;
  margin: 0.67em 0;
}

.noreset h2 {
  font-size: 1.5em;
  font-weight: bold;
  margin: 0.83em 0;
}

.noreset h3 {
  font-size: 1.17em;
  font-weight: bold;
  margin: 1em 0;
}

.noreset h4 {
  font-size: 1em;
  font-weight: bold;
  margin: 1.33em 0;
}

.noreset h5 {
  font-size: 0.83em;
  font-weight: bold;
  margin: 1.67em 0;
}

.noreset h6 {
  font-size: 0.67em;
  font-weight: bold;
  margin: 2.33em 0;
